
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'HelloWorld',
  props: {
    msg: String,
  },
  methods: {
    showToast() {
      this.$core.showToast({
        message: 'Hello Vue',
        duration: 'SHORT'
      });
    },
    camera() {
      this.$core.showCamera();
    }
  },
});
